@import "../../common/coloring.scss";

.card-item{
    background: $secondary-color;
    cursor: pointer;
    transition: all 0.3s;

    &:hover{
        transform: scale(1.07);
        transition: all 0.8s;
    }
}

.card-top{
    height: 300px;
    img{
        width: 100%;
        height: 100%;
    }
}

.card-bottom{
    padding:20px
}
.card-info{
    color:$font-primary;
    h4{
        font-size: 22px;
        font-weight: 400;
        margin-bottom:10px;
    }
}
