@import "../../common/coloring.scss";

.movie-section{
    display: flex;
    justify-content: space-evenly;
    padding: 40px 0px;
    color: $font-primary;
    font-weight:500;
}

.movie-rating{
    padding-left: 3px;
    margin-top: 20px;
    color: $font-primary;
    display: flex;
    span{
        margin-right: 20px;
    };
}

.movie-title{
    font-size: 40px;
    color:$font-primary;
}

.movie-plot{
    margin-top: 20px;
    line-height: 1.8rem;
}

.movie-info > div span:first-child{
    padding: 10px 0px;
    color: $font-primary;
    font-weight: 500;
    width: 100px;
    display: inline-block;
}

.movie-info > div span{
    color:$font-secondary;
}